import Vue from 'vue'
import VueRouter from 'vue-router'
import Island from '../views/Island.vue'
import Map from '../views/Map.vue'
import Ship from '../views/Ship.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Island',
        component: Island
    },
    {
        path: '/map',
        name: 'Map',
        component: Map
    },
    {
        path: '/ship',
        name: 'Ship',
        component: Ship
    },
]

const router = new VueRouter({
    mode: 'history',
    routes
})

export default router